@import "../app.module.css";

.videoList {
  display: flex;
  flex: 1 1 20%;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 768px) {
  .videoList {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
